body {
background-color: #0d0d0d;
font-family: "GraphikRegular", sans-serif;
min-width: 320px;
text-align: center;
margin: 0;
cursor: url('https://cdn.glitch.com/f594f5a0-5612-49ac-a1d8-b4db86ddd583%2Fcursor.png?1534566432212');	
}

.text {
font-size: 32px;
color: #fafafa;
width: 100%;
margin: 0 auto;
white-space: nowrap;
overflow: hidden;
text-shadow: 1px 1px 24px #FFFFFF;
position: fixed;
top: 0;
right: 0;
margin-top: 24px;
z-index: 1
}

.text span {
display: inline-block;
padding-left: 100%;
text-indent: 0;
animation: text 18s linear infinite;
}

.text span:hover {
animation-play-state: paused
}

.link {
color: white;
cursor: url('https://cdn.glitch.com/f594f5a0-5612-49ac-a1d8-b4db86ddd583%2Fcursor.png?1534566432212'), auto;	
}

.link:hover {
cursor: url('https://cdn.glitch.com/f594f5a0-5612-49ac-a1d8-b4db86ddd583%2Fcursor.png?1534566432212'), auto;	
}

.elsewhere {
display: inline-block;
position: fixed;
bottom: 0;
right: 0;
margin: 0 32px 32px 0;
z-index: 1;
}

.twitter {
font-size: 32px;
color: #fafafa;
text-shadow: 0px 1px 20px #FFFFFF;
text-decoration: underline;
margin-left: 32px;
cursor: url('https://cdn.glitch.com/f594f5a0-5612-49ac-a1d8-b4db86ddd583%2Fcursor.png?1534566432212'), auto;	
}

/* Make it move */
@keyframes text {
  0%   { transform: translate(0, 0); }
  100% { transform: translate(-100%, 0); }
}

a-scene {
clear: both;
border: 0;
padding-bottom: 100vh;
position: absolute;
width: 100%;
z-index: 0;
cursor: url('https://cdn.glitch.com/f594f5a0-5612-49ac-a1d8-b4db86ddd583%2Fcursor.png?1534566432212'), auto;	
}

a-scene:hover {
cursor: url('https://cdn.glitch.com/f594f5a0-5612-49ac-a1d8-b4db86ddd583%2Fcursor.png?1534566432212');	
}

a-scene:active { 
cursor: url('https://cdn.glitch.com/f594f5a0-5612-49ac-a1d8-b4db86ddd583%2Fcursor.png?1534566432212'), auto;	
}

.a-inspector-loader { 
display:none; 
}